@import url(https://fonts.googleapis.com/css2?family=IBM+Plex+Mono:ital,wght@1,700&display=swap);
.header-container {
  padding: 30px;
  width: 84%;
}

.header {
  padding-left: 40px;
  display: flex;
  justify-content: space-between;
  background-color: white;
  box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}

.header .header__title {
  font-weight: 300px;
  font-family: "IBM Plex Mono", monospace;
  font-size: 25px;
}

.header .header__info {
  padding-top: 50px;
  margin-right: 50px;
}

.nav-container {
  position: fixed;
  width: 16%;
  right: 0px;
  top: 100px;
  padding-right: 25px;
}

.nav {
  display: flex;
  flex-direction: column;
  background-color: white;
  padding: 30px 20px;
  box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}

.nav a {
  text-decoration: none;
  color: #0008fc;
  text-transform: uppercase;
  font-size: 12px;
  text-align: center;
  font-weight: 600;
}

.nav a:not(:last-child) {
  margin-bottom: 30px;
}

.movies .movie {
  width: 47%;
  background-color: white;
  margin-bottom: 70px;
  display: flex;
  flex-direction: column;
  /* align-items: flex-start; */
  /* justify-content: space-between; */
  font-weight: 300;
  padding: 20px;
  padding-bottom: 0px;
  border-radius: 5px;
  color: #adaeb9;
  box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}

.movie .movie__chart {
  position: relative;
  bottom: -30px;
  max-height: 230px;
  width: 100%;
  /* margin-right: 30px; */
  box-shadow: 0 30px 60px -12px rgba(50, 50, 93, 0.25),
    0 18px 36px -18px rgba(0, 0, 0, 0.3), 0 -12px 36px -8px rgba(0, 0, 0, 0.025);
}

.movie .movie__title,
.movie .movie__year {
  margin: 0;
  font-weight: 300;
}

.movie .movie__title {
  margin-bottom: 5px;
  font-size: 24px;
  color: #2c2c2c;
}

.movie .movie__genres {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  margin: 5px 0px;
}

.movie__genres li,
.movie .movie__year {
  margin-right: 10px;
  font-size: 14px;
}

/* html, */
/* body, */
/* #potato, */
.container {
  padding: 30px;
  height: 100%;
  display: flex;
  justify-content: center;
  width: 84%;
}

.loader {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 300;
}

.movies {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  flex-wrap: wrap;
  padding-left: 20px;
  padding-right: 50px;
  padding-top: 40px;
  width: 90%;
}

.trend-container {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  width: 84%;
  padding: 0 30px;
}

.trend__board {
  box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
  padding: 20px;
  background-color: white;
  margin: 0 auto;
  width: 100%;
}

.trend__graph {
  width: 100%;
  height: 50vh;
}

.trend__comment {
  box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
  padding: 20px;
  background-color: white;
  margin: 0 auto;
  width: 100%;
  margin-top: 20px;
}

* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  background-color: #eff3f7;
  height: 100%;
}

